import { client } from "ontology-dapi"

const contract = CONFIG.contract; // 
const typeMap = new Map([
    ["get", "invokeRead"],
    ["GET", "invokeRead"],
    ["post", "invoke"],
    ["POST", "invoke"]
]);

/**
 *ontology
 *
 * @export
 * @param {*} {
 *     args = [], 
 *     type = 'get',  
 *     type = 'post'  
 *     method api
 * }
 * @returns
 */
export function ontDApi({ args = [], type = "get", method }, contractP) {
    return new Promise((resolve, reject) => {
        const _contract = contractP || contract
        const options = {
            ..._contract, // 
            operation: method, // 
            args // 
        };
        // console.log(options);
        client.api.smartContract[typeMap.get(type)](options)
        .then(res => resolve(res), err => reject(err))
        .catch(err => reject(err))
    })
}