import store from '@/store'
import { sha256 } from 'js-sha256'
import { genDomainHash } from '@/tools/hex'
import { ontDApi } from '@/tools/ontDapi'

const root = CONFIG.root                                                    // 
// const resultCode = config.domainActionResultCode                         // 

export default {
    /**
     * 
     * @param {String} name 
     */
    rgister(name = '') {
        const args = [
            { type: 'ByteArray', value: store.state.scriptHash },           // scripthash
            { type: 'String', value: name },                                // test123.ont nametest123
            { type: 'ByteArray', value: sha256(root)},                      // hashtest123.ontont,sha256("ont")
            { type: 'ByteArray', value: store.state.inviterScriptHash}      // scripthash
        ]
        return ontDApi({
            args,
            type: 'post',
            method: 'Register'
        })
    },

    /**
     * 
     * @param {String} name
     * @param {Number} code
     * @param {String} content
     * @returns
     */
    setResolve(name, code, content) {
        const args = [
            { type: 'ByteArray', value: store.state.scriptHash },           // scripthash
            { type: 'ByteArray', value: genDomainHash(name) },              // hash
            { type: 'Number', value: code},                                 // 
            { type: 'String', value: content}                               // 
        ]
        console.log(args)
        return ontDApi({
            args,
            type: 'post',
            method: 'SetResolve'
        })
    },

    /**
     * 
     * @param {String} name 
     * @param {Number} code 
     * @returns
     */
    resolve(name, code) {
        const args = [
            { type: 'ByteArray', value: genDomainHash(name) },              // hash
            { type: 'Number', value: code},                                 // 
        ]
        return ontDApi({
            args,
            method: 'Resolve'
        })
    },

    /**
     * 
     * @param {String} to address
     * @param {String} name 
     * @returns
     */
    transfer(to, name) {
        const args = [
            { type: 'ByteArray', value: store.state.scriptHash },           // scripthash
            { type: 'ByteArray', value: to },                               // scripthash
            { type: 'ByteArray', value: genDomainHash(name)},               // hash
        ]
        return ontDApi({
            args,
            type: 'post',
            method: 'Transfer'
        })
    },

    /**
     * 
     * @param {String} name 
     * @returns
     */
    drop(name) {
        const args = [
            { type: 'ByteArray', value: store.state.scriptHash },           // scripthash
            { type: 'ByteArray', value: genDomainHash(name) },              // hash
        ]
        return ontDApi({
            args,
            type: 'post',
            method: 'Drop'
        })
    },

    /**
     * 
     * @param {String} name 
     * @returns
     */
    queryByuser(name) {
        const args = [
            { type: 'ByteArray', value: genDomainHash(name) },              // hash
        ]
        return ontDApi({
            args,
            method: 'GetDomainReg'
        })
    },

}